@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600;800&family=Sora:wght@600&display=swap');

*{
    font-family: Poppins, sans-serif;
}

.gameCard{
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    gap: 1rem;
    padding: 1rem;

    width: 500px;
    min-width: 300px;
    height: 600px;

    background-color: black;
    border-radius: 1rem;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

@media screen and (max-width: 600px) {
    .gameCard{
        margin: 0 1rem;
    }
}

.gameTitle{
    font-size: 1.5rem;
    font-weight: 600;
    color: white;
}

.gameImage{
    height: 300px;
    object-fit: cover;
    border-radius: .5rem;
}

.gamePrice{
    font-weight: 600;
    color: white;
    font-size: 1.5rem;
}

.addToCart{
    background-color: #6229ff;
    width: fit-content;
    padding: .5rem 2rem;
    align-self: flex-start;
    color: white;
    border-radius: 3px 0px 0px 3px;
    font-weight: 600;
    outline: none;
    border: none;
    transition: background-color .2s;
}

.addToCart:hover{
    cursor: pointer;
    background-color: #895eff;
}

.platforms p{
    color: white;
}

.platforms-list{
    display: flex;
    gap: .5rem;
    flex-wrap: wrap;
}

.platform-name{
    color: white;
    border: 1px solid white;
    border-radius: 1rem;
    font-size: .8rem;
    padding: .5rem 1rem;
    transition: background-color .2s;
}

.platform-name:hover{
    cursor: pointer;
    background-color: #6229ff;
}

.cardButtonSection{
    display: flex;
    align-items: center;
    color: white;
    font-weight: 600;
}

.quantity{
    display: flex;
    align-items: center;
    background-color: #6229ff;
    height: 100%;    
    border-radius: 0px 3px 3px 0px;
    padding: 0 1rem;
    border-left: 2px solid #4419b9;
}